.js-form {
  max-width: 600px;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.js-inputs-container {
  width: 100%;
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.inputs {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
}

.add-country {
  justify-content: center;
  padding: 20px;
  display: flex;
}

.js-add-country, .js-form-submit-btn {
  cursor: pointer;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.js-add-country:hover, .js-form-submit-btn:hover {
  background-color: #45a049;
}

.card img {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}

@media screen and (max-width: 768px) {
  .js-form {
    padding: 10px;
  }

  .js-inputs-container {
    margin-bottom: 10px;
  }

  .inputs {
    margin-bottom: 8px;
    padding: 8px;
  }

  .js-add-country, .js-form-submit-btn {
    margin: 8px;
    padding: 8px 16px;
  }
}

.js-answers-from-form {
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-direction: column;
  flex: 0 0 calc(25% - 20px);
  align-items: center;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

@media screen and (max-width: 768px) {
  .card {
    flex: 0 0 calc(50% - 20px);
  }
}

.card-info {
  text-align: center;
}

.card-country {
  margin-bottom: .5vw;
  font-size: 2.5vw;
}

.card-city {
  margin-bottom: 1.5vw;
  font-size: 2vw;
}

.card-icon {
  width: 10vw;
  height: 10vw;
  margin-bottom: 1vw;
}

.card-text {
  margin-bottom: .5vw;
  font-size: 3vw;
}

.card-temp {
  font-size: 3vw;
}

